import { Line } from 'konva';

class Wire extends Line {
    constructor(points) {
        super({
            points: points,
            stroke: 'black',
            strokeWidth: 1
        });

        /* parameters */
        this.type = 'Wire';
        this.sideOne = null;
        this.sideTwo = null;
        this._id = Math.floor(Math.random() * 1000);

        /* handlers */
        this.on('dblclick', () => {
            console.log('delete wire');
            this.sideOne.stand.panel.draw();
            this.sideOne.disconnect(this);
            this.sideTwo.disconnect(this);
            this.destroy();
            this.sideOne.stand.panel.draw();
        });
    }

    setSides(inputOne, inputTwo) {
        this.sideOne = inputOne;
        this.sideTwo = inputTwo;
    }
}

export default Wire;


