import { Rect, Label, Text } from 'konva';
import Input from './Input.js';

class Meter extends Rect {
    constructor(x, y, param, stand) {
        super({
            x: x,
            y: y,
            width: 25,
            height: 12.5,
            fill: 'rgba(12,12,12,0.1)',
            stroke: 'black',
            strokeWidth: 1
        });

        /* parameters */
        this.type = 'Meter';
        this.value = 12;
        this.param = param;
        this.stand = stand;
        this.x = this.attrs.x;
        this.y = this.attrs.y;
        this._id = Math.floor(Math.random() * 1000);
        this.inputOne = new Input(this.x - 10, parseInt(this.y) + 6.25, this.stand, this);
        this.inputTwo = new Input(this.x + 35, parseInt(this.y) + 6.25, this.stand, this);
        this.display = new Label({
            x: this.x,
            y: this.y,
        });

        this.display.add(new Text({
            text: this._getValue(),
            fontFamily: 'Calibri',
            fontSize: 10,
            padding: 2,
            fill: 'black'
        }));
        this.stand.addElements([this.inputOne, this.inputTwo, this.display]);
        /* handlers */

    }

    _getValue() { return `${this.value} ${this.param}`; }

    check() {
        console.log('CHECK METER')
        console.log(this.stand);
    }

}

export default Meter;


