import Stand from './StandComponents/Stand.js';

class App {
  constructor() {
    this.stand = new Stand();
  }

  start(data) {
    this.stand.build(data);
  }

  update(data) {
    this.stand.update(data);
  }

  _getStandArray() {
    return this.stand.getAsArray();
  }
}

export default App;



