import { Rect, Label, Text } from 'konva';
import Input from './Input.js';

class Resister extends Rect {
    constructor(x, y, val, stand) {
        super({
            x: x,
            y: y,
            width: 40,
            height: 12.5,
            stroke: 'black',
            strokeWidth: 1,
            fill: 'rgba(12,12,12,0.1)'
        });

        /* parameters */
        this.type = 'Resister';
        this.x = this.attrs.x;
        this.y = this.attrs.y;
        this.val = val;
        this.value = `${this.val} Om`;
        this.stand = stand;
        this._id = Math.floor(Math.random() * 1000);
        this.inputOne = new Input(this.x - 10, parseInt(this.y) + 6.25, this.stand, this);
        this.inputTwo = new Input(this.x + 50, parseInt(this.y) + 6.25, this.stand, this);
        this.display = new Label({
            x: this.x,
            y: this.y,
        });

        this.display.add(new Text({
            text: this.value,
            fontFamily: 'Calibri',
            fontSize: 10,
            padding: 2,
            fill: 'black'
        }));
        this.stand.addElements([this.inputOne, this.inputTwo, this.display]);
        /* handlers */

    }
}

export default Resister;


