'use strict';

import { Circle } from 'konva';
import Wire from './Wire.js';

function checkInput(x, y, input) {
    for (let component of input.stand.components) {
        if (component.type === 'Input') {
            if (component.connectedInputs.length < 2) {
                if (parseInt(component.x) + 8 >= x && parseInt(component.x) - 8 <= x && parseInt(component.y) + 8 >= y && parseInt(component.y) - 8 <= y) {
                    return {
                        result: true,
                        input: component
                    };
                }
            }
        }
    }
    return {
        result: false
    };
}

function wiring(e, _this) {
    _this.wire.points([
        _this.x,
        _this.y,
        e.evt.offsetX,
        e.evt.offsetY
    ]);
    _this.stand.panel.draw();
}

function diswiring(e, _this) {
    let check = checkInput(e.evt.offsetX, e.evt.offsetY, _this);
    if (check.result === true && (_this.owner !== check.input.owner)) {
        _this.connect(check.input);
    } else {
        _this.wire.destroy();
        _this.pointer.destroy();
        _this.stand.panel.draw();
    }
    _this.connecting = false;
}

class Input extends Circle {
    constructor(x, y, stand, owner = null) {
        super({
            x: x,
            y: y,
            radius: 4,
            stroke: 'black',
            strokeWidth: 1,
            fill: 'rgba(12,12,12,0.2)'
        });

        /* parameters */
        this.stand = stand;
        this.x = this.attrs.x;
        this.y = this.attrs.y;
        this.connectedInputs = [];
        this.connecting = false;
        this.type = 'Input';
        this.pointer = null;
        this.owner = owner;
        this._id = Math.floor(Math.random() * 1000);

        /* handlers */
        this.on('mouseover', () => {
            if (this.connectedInputs.length < 2 && this.pointer !== null) {
                this.pointer.destroy();
                this.pointer = null;
            }
            if (this.connectedInputs.length < 2 && this.pointer === null) {
                this.pointer = new Circle({
                    x: this.x,
                    y: this.y,
                    radius: 8,
                    draggable: true
                });
                this.stand.addElement(this.pointer);
                this.pointer.on('dragstart', () => {
                    this.wire = new Wire([this.x, this.y]);
                    this.stand.addElement(this.wire);
                    this.connecting = true;
                });
                this.pointer.on('dragmove', (e) => {
                    wiring(e, this);
                });
                this.pointer.on('dragend', (e) => {
                    diswiring(e, this);
                });
            }
        });
        this.on('dblclick', () => {
            this.disconnect();
        });
    }

    connect(input) {
        this.wire.points([
            this.x,
            this.y,
            input.x,
            input.y
        ]);
        this.wire.setSides(this, input);
        this.connectedInputs.push(input);
        input.connectedInputs.push(this);
        if (input.connectedInputs.length === 2) {
            input.pointer.destroy();
        }
        this.connecting = false;
        this.pointer.destroy();
        this.pointer = null;
        this.stand.panel.draw();

        console.log(`this.owner.type === ${this.owner.type}`);
        console.log(`input.type === ${input.type}`);
        if (this.owner !== null && this.owner.type === 'Meter') this.owner.check();
        if (input.owner !== null && input.owner.type === 'Meter') input.owner.check();

    }

    disconnect(wire) {
        if (wire) {
            this.connectedInputs = this.connectedInputs.map((input) => {
                if (wire.sideOne !== input && wire.sideTwo !== input) {
                    return input;
                }
            });
        }
        this.pointer = null;
        this.wire = null;
    }
}

export default Input;


