'use strict';

import { Stage, Layer } from 'konva';
import Input from './Input.js';
import Meter from './Meter.js';
import Resister from './Resister.js';
import Picker from './Picker.js';

class Stand extends Stage {
    constructor() {
        let standWrapper = document.getElementById('stand');
        let width = standWrapper.scrollWidth;
        let height = standWrapper.clientHeight;
        super({
            container: 'stand',
            width: width,
            height: height
        });
        this.panel = new Layer();
        this.components = [];
        this.add(this.panel);
    }

    build(data) {
        console.log('Building stand...');
        let elements = this.parseJSON(data);
        for (let element of elements) {
            this.addElement(element);
        }
        this.add(this.panel);
    }

    update(data) {
        this.panel.destroyChildren();
        this.components = [];
        this.build(data);
    }

    addElement(element) {
        if (element.type !== 'undefined')
            this.components.push(element);
        this.add(this.panel.add(element));
    }

    addElements(elements) {
        for (let element of elements) {
            if (element.type !== 'undefined')
                this.components.push(element);
            this.add(this.panel.add(element));
        }
    }

    parseJSON(data) {
        let elements = [];
        for (let el of data) {
            if (el.type) {
                switch (el.type) {
                    case 'Input':
                        elements.push(new Input(el.x, el.y, this));
                        break;
                    case 'Meter':
                        elements.push(new Meter(el.x, el.y, el.param, this));
                        break;
                    case 'Resister':
                        elements.push(new Resister(el.x, el.y, el.value, this));
                        break;
                    case 'Picker':
                        elements.push(new Picker(el.x, el.y, el.data, el.param, this));
                        break;
                }
            }
        }
        return elements;
    }

    getAsArray() {
        let arrayToParse = this.components.filter((element) => {
            return ((element.type === 'Input' && element.owner === null) || (element.type !== 'Input')) && (element.type);
        });
        return arrayToParse.map((element) => {
            let el = {};
            switch (element.type) {
                case 'Input':
                    let inputs = [];
                    if (element.connectedInputs.length === 1) {
                        inputs.push(element.connectedInputs[0]._id);
                    }
                    if (element.connectedInputs.length === 2) {
                        inputs.push(element.connectedInputs[0]._id);
                        inputs.push(element.connectedInputs[1]._id);
                    }
                    el = {
                        'type': element.type,
                        'x': element.x,
                        'y': element.y,
                        'connectedInputs': inputs
                    };
                    break;
                case 'Meter':
                    el = {
                        'type': element.type,
                        'x': element.x,
                        'y': element.y,
                        'param': element.param,
                        'connects': [
                            element.inputOne._id,
                            element.inputTwo._id
                        ]
                    };
                    break;
                case 'Wire':
                    el = {
                        'type': element.type,
                        'x': element.x,
                        'y': element.y,
                        'sides': [
                            element.sideOne._id,
                            element.sideTwo._id
                        ]
                    };
                    break;
                case 'Resister':
                    el = {
                        'type': element.type,
                        'x': element.x,
                        'y': element.y,
                        'val': element.val,
                        'connects': [
                            element.inputOne._id,
                            element.inputTwo._id
                        ]
                    };
                    break;
                case 'Picker':
                    el = {
                        'type': element.type,
                        'x': element.x,
                        'y': element.y,
                        'data': element.states,
                        'param': element.param
                    };
                    break;
                default:
                    break;
            }
            return el;
        });
    }
}

export default Stand;


