'use strict';

import { Circle, Line, Rect, Label, Text } from 'konva';

class Picker extends Circle {
    constructor(x, y, data, param, stand) {
        super({
            x: x,
            y: y,
            radius: 7.5,
            fill: 'transparent',
            stroke: 'grey',
            strokeWidth: 1.5
        });

        /* parameters */
        this.x = this.attrs.x;
        this.y = this.attrs.y;

        this.background = new Rect({
            x: this.x - 20,
            y: this.y - 20,
            width: 40,
            height: 30,
            fill: 'rgba(12,12,12,0.1)',
            stroke: 'black',
            strokeWidth: 1
        });

        this.display = new Label({
            x: this.x - 15,
            y: this.y - 20,
        });

        this.display.add(new Text({
            text: 'test',
            fontFamily: 'Calibri',
            fontSize: 10,
            padding: 2,
            fill: 'black'
        }));

        this.pointer = new Line({
            points: [parseInt(this.x),
                parseInt(this.y),
                parseInt(this.x) + parseInt(this.state),
                parseInt(this.y) + parseInt(this.state)
            ],
            stroke: 'black',
            strokeWidth: 1
        });

        this.param = param;
        this.stand = stand;
        this.stateId = 0;
        this.states = this.parseDataToStates(data);
        this.updateState();
        this._id = Math.floor(Math.random() * 1000);

        this.stand.addElements([this.pointer, this.background, this.display]);

        /* handlers */
        this.on('click', this.clickHandler.bind(this));
    }

    clickHandler() {
        this.stateId++;
        this.updateState();
    }

    updateState() {
        if (this.stateId === this.states.length) this.stateId = 0;

        this.state = this.states[this.stateId];
        this.pointer.points([parseInt(this.x),
            parseInt(this.y),
            parseInt(this.state.x),
            parseInt(this.state.y)
        ]);
        this.display.children[0].text(`${this.state.value} ${this.param}`);
        this.stand.panel.draw();
    }

    parseDataToStates(data) {
        let states = [];
        let step = 180 / data.length;
        data.map((el, i) => {
            states.push({
                x: this.x - (5 + i) * Math.cos(i),
                y: this.y - 5 * Math.round(Math.sin(i)),
                value: el
            });
        });
        return states;
    }
}

export default Picker;


