'use strict';

require('./lib/styles/style.css');
const axios = require('axios');
let main, app;

window.onload = () => {
    setTimeout(() => {
        main = document.getElementById('main');
        main.className = '';
        let preloader = document.getElementById('preloader');
        preloader.className = 'hide';

        let App = require('./lib/main');
        let labs = require('./lib/storage/data');
        app = new App();

        let labSelector = document.getElementById('selectLab');
        let labId = labSelector.value;
        let data = labs[labId];

        let labDoc = document.getElementById('labDoc');
        labDoc.innerHTML = `<embed id="labPdf" class="pdf" src="${labs[labId].doc}"/>`;
        let labPdf = document.getElementById('labPdf');
        labDoc.style.height = labPdf.clientHeight;
        labDoc.style.width = labPdf.clientWidth;

        labSelector.addEventListener('change', (e) => {
            console.log(e.target.value);
            data = labs[e.target.value];
            labDoc.innerHTML = `<embed id="labPdf" class="pdf" src="${data.doc}"/>`;
            app.update(data.stand);
        });

        app.start(data.stand);

        /* buttons */
        let resetBtn = document.getElementById('resetBtn');
        resetBtn.addEventListener('click', () => {
            app.update(data.stand);
        });

        let sendBtn = document.getElementById('sendBtn');
        let instance = axios.create({
            baseURL: 'https://virlab.herokuapp.com'
        });

        sendBtn.addEventListener('click', () => {
            let data = app._getStandArray();

            console.log(data);

            instance.post('/check', {
                data: data
            }).then((response) => {
                console.log(response);
            }).catch((error) => {
                console.log(error);
            });
        });

        let saveBtn = document.getElementById('saveBtn');
        saveBtn.addEventListener('click', (e) => {
            showModal('save');
        });
        let loadBtn = document.getElementById('loadBtn');
        loadBtn.addEventListener('click', (e) => {
            showModal('load');
        });
        let importBtn = document.getElementById('importBtn');
        saveBtn.addEventListener('click', (e) => {
            showModal('save');
        });
        let exportBtn = document.getElementById('exportBtn');
        loadBtn.addEventListener('click', (e) => {
            showModal('load');
        });
    }, 0);
};

function showModal(type) {
    var dialog = document.getElementById(`${type}Dialog`);
        dialog.className="dialog";
        dialog.scrollIntoView();
        
    main.className = "inDialog";
          

    var form = document.getElementById(`${type}Form`);
    var submitBtn = document.getElementById(`${type}DialogBtn`);
        var listener = type === 'save' ? saveListener : loadListener;
            listener.form = form;
        submitBtn.addEventListener('click', listener);
    var close = document.getElementById(`close${type}Btn`);
    close.addEventListener('click', (e) => {
        main.className = "";
        submitBtn.removeEventListener('click', listener);
        dialog.className = "dialog hide";
    });
};

function saveListener(e) {
    e.preventDefault();            
    if (this.form.user.value === '') {
        this.form.children[0].children[0].innerHTML = "Введите имя";
    } else {
        console.log('Saving...');
        console.log(app._getStandArray());
        axios.create({
            baseURL: 'https://virlab.herokuapp.com'
        }).post('/save', {
            user: this.form.children[0].children[2].value,
            stand: app._getStandArray()
        }).then((res) => {
            console.log(res);
        }).catch((err) => {
            console.error(err);
        });
    }
}

function loadListener(e) {
    e.preventDefault();            
    if (this.form.user.value === '') {
        this.form.children[0].children[0].innerHTML = "Введите имя";
    } else {
        console.log('Loading...');
        axios.create({
            baseUrl: 'https://virlab.herokuapp.com'
        }).post('/load', {
            user: this.form.children[0].children[2].value
        }).then((res) => {
            console.log(res);
        }).catch((err) => {
            console.error(err);
        });
    }
}


